import dictionform from './dictionComponents/dictionForm'
import { BUSINESSTYPE_GETALL } from 'type'
import {
	sysDictionAdd,
	sysDictionDelete,
	sysDictionUpdate,
	sysDictionPages
} from 'api/api'

let dictionForm = {
	dictType: '',
	dictCode: '',
	dictZhCnName: '',
	dictJaJpName: '',
	dictKoKrName: '',
	dictEnUsName: '',
	dictZhCnDescription: '',
	dictJaJpDescription: '',
	dictKoKrDescription: '',
	dictEnUsDescription: '',
	isSys: true,
	seq: '',
	ext1: '',
	ext2: '',
	ext3: ''
}
export default {
	data() {
		return {
			//				select:'name',

			pageData: {},
			searchValue: '',
			sertchType: 'name',
			page: {
				start: 0,
				length: 10,
				draw: 1,
			},
			currentPage: 1,
			pageSizes: [10, 20, 30],
			dialogVisible: false,
			dialogTyle: '',
			dictionForm: {}
		}
	},
	components: {
		dictionform
	},

	created() {
		this.init(this.page)
	},
	watch: {
		searchValue(value) {
			if (!value) {
				this.init(this.page)
			}
		}
	},
	methods: {
		init(data) {
			sysDictionPages(data).then(res => {
				this.pageData = res.data.obj
			})
		},
		showDilog(type, data = dictionForm) {
			this.dictionForm = {
				...data
			}
			this.dialogVisible = true
			this.dialogTyle = type
		},
		onQuery(key) {
			if (key) this.sertchType = key
			this.searchValue && this.init({
				...this.page,
				[this.sertchType]: this.searchValue
			})
		},
		onSubmit() {
			this.$refs.dictionForm.validate(valid => valid && this.commitForm())
		},
		commitForm() {
			if (this.dialogTyle === 'edit') {
				delete this.dictionForm.createTime
				delete this.dictionForm.modifyTime
				sysDictionUpdate(this.dictionForm).then(res => {
					this.init(this.page)
					this.dialogVisible = false
				})
			} else if (this.dialogTyle === 'add') {
				sysDictionAdd(this.dictionForm).then(res => {
					this.init(this.page)
					this.dialogVisible = false
				})
			}
		},

		handleDelete(data) {
			this.$confirm(this.$t('commom.deleConfim'), '', {
				confirmButtonText: this.$t('commom.sure'),
				cancelButtonText: this.$t('commom.cancel'),
				type: 'warning'
			}).then(() => {
				sysDictionDelete({
					dictId: data.dictId
				}).then(res => {
					this.init(this.page)
				})
			})

		},
		handleSizeChange(val) {
			this.page.length = val
			this.page.start = (this.currentPage - 1) * this.page.length
			this.init(this.page)
		},
		handleCurrentChange(val) {
			this.currentPage = val;
			this.page.start = (this.currentPage - 1) * this.page.length
			this.init(this.page)
		}
	}
}



